.container {
  width: 1200px;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  /* width: 100%; */
  justify-content: flex-start;
}
.contentWrapper {
  display: flex;
  flex-direction: column;
  width: 700px;
  flex-wrap: wrap;
  font-weight: 400;
}

.addittional {
  width: 900px;
  display: flex;
  flex-direction: column;
}

.poster {
  margin-right: 15px;
  border-radius: 40px;
}

.title {
  font-weight: 900;
  font-size: 30px;
  margin: 0;
  padding: 0;
}

.additionalList {
  display: flex;
  flex-direction: column;
  width: 900px;
  margin: 0;
  padding: 0;
  margin-bottom: 20px;
}
.additionalListLink {
  padding-left: 10px;
  font-size: 18px;
  font-weight: bold;
}

.additionalListLinkActive {
  color: rgb(206, 24, 24);
}

.backBTN {
  position: relative;
  text-transform: uppercase;
  color: white;
  font-weight: bold;
  text-decoration: none;
  text-shadow: -1px -1px #000;
  user-select: none;
  padding: 0.8em 2em;
  margin-bottom: 10px;
  margin-left: 20px;
  outline: none;
  background-color: #000;
  background-image: linear-gradient(
      45deg,
      rgba(255, 255, 255, 0) 30%,
      rgba(255, 255, 255, 0.8),
      rgba(255, 255, 255, 0) 70%
    ),
    radial-gradient(
      190% 100% at 50% 0%,
      rgba(255, 255, 255, 0.7) 0%,
      rgba(255, 255, 255, 0.5) 50%,
      rgba(0, 0, 0, 0) 50%
    );
  background-repeat: no-repeat;
  background-size: 200% 100%, auto;
  background-position: 200% 0, 0 0;
  box-shadow: rgba(0, 0, 0, 0.3) 0 2px 5px;
}

.backBTN:active {
  top: 1px;
  box-shadow: none;
}

.backBTN:hover {
  transition: 0.5s linear;
  background-position: -200% 0, 0 0;
}
